<template>
    <div align="center">
        <!--   -->
        <table>
            <tr>
                <td></td> 
                <td></td>
                <td></td>
            </tr>
            <!--    -->
             <tr :key="index" v-for="(value, index) in data" @click="detail(index)">
                <td>{{value.writer}}</td>
                <td>{{value.title}}</td>
                <td>{{value.content}}</td>
            </tr>
        </table>
        <!--   write  -->
        <button @click="write"></button>
    </div>
</template>
<script>
import data from "@/data"

export default {
    name: 'Read',
    data() {
        return {
            data: data
        }
    },
    methods: {
        // create  
        write() { 
            this.$router.push({
                path: 'create'
            })
        },
        detail(index) {
            this.$router.push({
				name: 'detail',
				path: 'detail',
                params: {
                    contentId: index
                }
            })
        }
    }
}
</script>



