<template>
    <div>
        <input v-model="writer" placeholder=""/>
        <input v-model="title" placeholder=""/>
        <textarea v-model="content" placeholder=""/>
        <!--   write -->
        <button @click="index !== undefined ? update() : write()">{{index !== undefined ? '' : ''}}</button>
    </div>
</template>
<script>
import data from '@/data' // data

export default {
    name: 'Create',
    data() { 
        const index = this.$route.params.contentId;
        return {
            // v-model   
            data: data,
            // index   data (   )
            index: index,
            writer: index !== undefined ? data[index].writer : "", 
            title: index !== undefined ? data[index].title : "",
            content: index !== undefined ? data[index].content :"" 
        }
    },
    methods: {
        write() {  // write 
            this.data.push({ //    push
                writer: this.writer,
                title: this.title,
                content: this.content 
            })
            this.$router.push({ //  read() 
                path: '/read'
            })
        },
        update() {
            data[this.index].writer = this.writer
            data[this.index].title = this.title
            data[this.index].content = this.content
            this.$router.push({
                path: '/read'
            })
        }
    }
}
</script>


