<template>
    <div>
        <div>{{data.writer}}</div> 
        <div>{{data.title}}</div>
        <div>{{data.content}}</div>
        <button @click="updateData"></button>
        <button @click="deleteData"></button>
    </div>
</template>

<script>
import data from '@/data'
export default {
    name: 'Detail',
    data() { // index  route param  contentId 
        const index = this.$route.params.contentId
        return {
            data: data[index], // data      
            index: index,
        }
    },
    methods: {
        // splice .splice(,1);  
        deleteData() {
             data.splice(this.index, 1)
             this.$router.push({
                 path: '/read'
             })
        },
        updateData() {
            this.$router.push({
				name: 'create',
				path: 'create',
                params: {
                    contentId: this.index
                }
            })
        }
    }
}
</script>





